.container{
    width: 80%;
    background-color:#e7e8f3;
    overflow:scroll;
}
.container_header {
    height: 70px;
    border-bottom:1px solid #403b3b21;
}
ul.navbar_menu{
    height: 32px;
    padding: 3px;
    margin: 0 auto;
    border-radius: 15px;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: hsla(0,0%,100%,0.3);
    width: fit-content;
    margin-bottom: 20px;
}
.navbar_menu-item{
    display: flex;
    height: 100%;
    cursor: pointer;
    min-width: 120px;
    padding: 7px 0;
    padding-bottom: 11px;
    text-align: center;
    border-radius: 15px;
}
.navbar_menu-item.active{
    background-color:   hsla(0,0%,100%,0.3);
    color: #239292;
}
.navbar_menu-item span{
    width: 100%;
    height: 12px;
    font-size: 12px;
    text-align: center;
    line-height: 1.2rem;
    text-transform: uppercase;
}
.col{
    padding: 10px 70px;
    display: flex;
}
.row{
    width: 50%;
}
.story__items{
    padding: 0 15px;
}
.story__item{
    margin-bottom: 30px;
}
.story__item-container{
    padding: 20px;
    border-radius: 8px;
    background:#e7e8f3;
    box-shadow: 0 2px 10px rgba(66, 66, 66, 0.4);
}
.story__item-header {
    width: 100%;
    margin-bottom: 10px;
}
.story__header-info {
    margin-bottom: 10px;
    display: flex;
}
.story__header-info-img img{
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.story__header-info-name-singer {
    display: flex;
}
.story__header-info-singer {
    font-size: 13px;
    color: #000;
    display: flex;
}
.story__header-info-love {
    color: #239292;
    font-size: 12px;
    font-weight: 500;
    margin-left: 10px;
}
.story__item-header-stt p{
    font-size: 13px;
    font-weight: 300;
    color: #000;
}
.story__item-img{
    width: 100%;
    border-radius: 5px;
}
.story__item-action{
    display:flex;
    color: #fff;
    font-size: 14px;
    font-weight: 300;
}
.action-comment{
    margin-left: 10px;
}